.navbar {
    width: auto;
    padding: 20px 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color:none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: padding 0.3s;
}

.logobar {
    /* padding: 20px; */
    display: inline-flex;
    color: #d3d3d3;
    align-items: center;



}

.logobar img {
    height: 60px;
    /* align-items: center; */
    padding-right: 20px;
    transition: height 0.3s;
}

.logobar h2 {
    display: inline-block;
    align-items: center;
}

/* .navitems {
    display: flex;
    align-items: center;

} */

.navitems ul {
    display: flex;
    /* align-items: center; */
}

.navitems ul li {
    display: inline-block;
    list-style: none;
    padding: 13px;
    transition: padding 0.3s;
}

.navitems ul li a {
    text-decoration: none;
    position: relative;
    padding: 5px;
    color: #ffffff;
    display: block;

}

.navitems ul li a::after {
    content: '';
    width: 0%;
    height: 3px;
    background: #ff004f;
    position: absolute;
    left: 0;
    bottom: -6px;
    transition: 0.3s;


}

.navitems ul li a:hover::after {
    width: 100%;
}

.toggle-button {
    position: absolute;
    top: 2.2rem;
    right: 10%;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 25px;
    background: none;
    padding: 0;
    border: none;
    transition: 0.3s;
}

.blank {
    position: absolute;
    display: block;
    height: 100px;
}
.navbar.scrolled {

    background-color: #242424;
}

@media (max-width: 900px) {
    .logobar {
        padding-left: 10%;
    }

    .logobar h2 {
        display: none;
    }
        .logobar img {
            height: 50px;
        }



}

@media (max-width: 700px) {
    .toggle-button {
        display: flex;
    }

    .logobar img {
        height: 40px;
    }

    .navitems {
        position: fixed;
        display: none;
        width: 100%;
        top: 80px;
        background-color: #242424;
        padding-bottom: 20px;
    }

    .navbar {
        position: fixed;
        top: 0;
        width: 100%;
        /* flex-direction: column;
        align-items: flex-start; */
        padding: 30px 0px;
        z-index: 99;

    }

    .navitems ul {
        width: 100%;
        flex-direction: column;
        padding: 20px 0px 0px 0px;
        margin: 0;
    }

    .navitems ul li {
        text-align: center;
        padding: 10px;
        /* margin: 10px; */
    }

    .navitems ul li a {
        padding: 0;

    }

    .navitems.active {
        display: flex;
    }

    .navitems ul li a:hover::after {
        width: 0;
    }

    .navitems ul li:hover {
        background-color: #285748;
    }

}

.toggle-button .bar {
    height: 3px;
    width: 100%;
    background-color: #ffffff;
    border-radius: 10px;

}

@media (min-width: 700px) {
    .navbar.scrolled {
        padding: 10px 10%;
        z-index: 99;
        background-color: #242424;
    }
    .logobar.scrolled img {
        height: 40px;
    }
    .navitems.scrolled ul li {
    display: inline-block;
    list-style: none;
    padding: 0 13px;
    }
}

@media (max-width: 700px) {
    .logobar.scrolled img {
        height: 20px;       
        }
    .toggle-button.scrolled {
    top: 1.6rem;
    right: 10%;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 17px;
    }
    .toggle-button.scrolled .bar {
    height: 3px;
    width: 80%;
    background-color: #ffffff;
    border-radius: 5px;

}


