.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 0 auto;
    max-width: 1200px;
    /* position: absolute; */
}

.card {
    display: block;
    width: calc(30% );
    max-width: 400px;
    aspect-ratio: 1 / 1;
    margin: 10px auto;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
    /* overflow: hidden; */
    
    /* position: absolute; */
    perspective: 1000px;
}

.flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.card:hover .flip-card-inner {
    transform: rotateY(180deg);
}

.card-content-front,
.card-content-back {
    
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background: linear-gradient(rgb(0, 128, 128, 0.8), rgb(0, 128, 128, 0.8));
}

.card-content-front {
    position: absolute;
    /* background-color: #bbb;
    color: black; */
}

.card-content-back {
    position: relative;
    /* background-color: #2980b9;
    color: white; */
    
    transform: rotateY(180deg);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin:auto;
    text-align: center;
    text-decoration: none;
    color: #ffffff; 
}
